import Hosts from '../../utils/host.js'
export default{
	data(){
		return{
			appInfo: {},
			env: Hosts.ENV_PROD,
			envs: [
			  {
				name: 'prod',
				value: Hosts.ENV_PROD
			  },
			  {
				name: 'dev',
				value: Hosts.ENV_DEV
			  }
			]
		}
	},
	methods:{
		submit() {
			Hosts.setEnvToStorage(this.env);
			uni.reLaunch({
				url: '/pages/index/index'
			})
		},
		changeEnv(e){
			this.env = e.target.value
		}
	}
}
